





















import { Component, Vue, Prop } from 'vue-property-decorator'

@Component({})
export default class DataEmpty extends Vue {
	@Prop({
		default: true
	})
	infoVisible!: Boolean
	@Prop({
		default: ''
	})
	tips!: string

	goIndex() {
		this.$STORE.setStore.backHome()
	}
}
